import React, { FunctionComponent, useState, useEffect } from "react";
import "../styles/home.css";

interface HomeProps {
    setActivePage: (page: string) => void;
}

const Home: FunctionComponent<HomeProps> = ({ setActivePage }) => {
  const [text, setText] = useState("");
  const [cursor, setCursor] = useState(true);  // tat pour grer le clignotement du curseur
  const [showAdditionalContent, setShowAdditionalContent] = useState(false);  // Nouvel tat pour contrler l'affichage des contenus supplmentaires

  const firstPart = "Je m'appelle Marc Lin, tudiant en Brevet De Technicien Suprieur, Service Informatique aux Organisations, option Solutions Logicielles et Applications Mtiers (BAC+2)";
  const secondPart = "Passionn par les logiciels et fascin par lexpansion de lindustrie des technologies ces dernires annes.";

  useEffect(() => {
    let currentIndex = 0;
    const interval = setInterval(() => {
      setText((prevText) => {
        if (currentIndex < firstPart.length) {
          const nextChar = firstPart[currentIndex++];
          return prevText + nextChar;
        } else {
          clearInterval(interval);  // Arrter la frappe une fois le texte complet affich
          setShowAdditionalContent(true);  // Activer l'affichage du contenu supplmentaire seulement aprs la fin de la frappe
          return prevText;
        }
      });
    }, 40);  // Vitesse de frappe des caractres

    // S'assurer de nettoyer l'intervalle en cas de dmontage du composant
    return () => clearInterval(interval);
  }, []);

  // Fonction pour animer le curseur aprs que tout le texte a t crit
  useEffect(() => {
    if (showAdditionalContent) {
      const cursorInterval = setInterval(() => {
        setCursor(prevCursor => !prevCursor);  // Inverser l'tat du curseur pour le faire clignoter
      }, 530);

      return () => clearInterval(cursorInterval);
    }
  }, [showAdditionalContent]);

  return (
    <div className="home">
      <div className="card position-absolute home-card">
        <div className="card-body">
          <h4>Bienvenue sur mon portfolio.</h4>
          <h2>
            <strong className="home-card-strong">
              {text}
              {cursor && <span className="cursor">|</span>}
            </strong>
          </h2>
          <p>
            {secondPart}
            <div className="navbar-nav ms-auto">
              <a className="nav-link col-lg-3 col-md-4" href="#" onClick={() => setActivePage('Profile')}><strong> En savoir plus.</strong></a>
              <a className="nav-link col-lg-3 col-md-4" href="https://www.linkedin.com/in/marc-lin-80425a25a/" target="_blank" rel="noopener noreferrer"><strong> LinkedIn.</strong></a>
            </div>
          </p>
        </div>
      </div>
    </div>
  );
};

export default Home;
